<template>
  <div id="app">
    <div class="desc">
      <p> welcome to the space! </p>
      <p> Here a magical journey is about to begin! </p>
    </div>
    <div class="pic"><img src="../../assets/img/pearl.jpg" alt=""></div>
    
  </div>
</template>

<script>
export default {
  name: 'App'
}
</script>

<style scoped>
.desc{
  font: 700 18px '微软雅黑';
  text-shadow: 4px 2px 4px #bbb;
}

.pic{
  width: 600px;
  padding: 10px;
  margin: auto;
  overflow: hidden;
  box-shadow: 0 0 8px #ccc;
}
.pic img{
  width: 600px;
  transition: all .6s;
}
.pic:hover img{
  transition: all .6s;
  transform: scale(1.2);
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



